<template>
  <el-form :model="form" ref="validateForm" label-width="120px" class="demo-dynamic">
    <el-row v-loading="loading">
      <template v-for="(item, index) in form.list">
        <div class="row" :key="item.day">
          <el-form-item
            label="累计签到天数"
            :prop="'list.' + index + '.day'"
            :rules="{
              required: true,
              message: '累计签到天数不能为空',
              trigger: 'change',
            }"
          >
            <el-input v-model="item.day" placeholder="请填写累计签到天数"></el-input>
          </el-form-item>

          <el-form-item
            label="奖励类型"
            :prop="'list.' + index + '.type'"
            :rules="{
              required: true,
              message: '请选择奖励类型',
              trigger: 'change',
            }"
          >
            <el-select v-model="item.type" placeholder="请选择奖励类型">
              <el-option
                v-for="typeItem in reward_type_list"
                :key="typeItem.value"
                :label="typeItem.label"
                :value="typeItem.value"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item
            label="奖励值"
            :prop="'list.' + index + '.value'"
            :rules="{
              required: true,
              message: '奖励值不能为空',
              trigger: 'change',
            }"
          >
            <el-input v-model="item.value" placeholder="请填写奖励值"></el-input>
          </el-form-item>

          <el-form-item class="delete">
            <el-button @click.prevent="removeConfig(index)" v-show="form.list.length > 1">删除</el-button>
            <el-button @click="addConfig" v-show="form.list.length - 1 === index">添加</el-button>
          </el-form-item>
        </div>
      </template>

      <el-form-item label="签到规则">
        <el-input v-model="form.sign_rule" type="textarea" :rows="4" placeholder="请填写签到规则"></el-input>
      </el-form-item>

      <el-form-item label="签到背景图片">
        <el-upload
          :limit="1"
          :headers="myHeaders"
          :action="'/v1/api/api/api/upload'"
          :on-success="fileSuccess"
          :before-upload="fileSuccessBefore"
          :file-list="file"
          list-type="picture"
        >
          <el-button size="small" type="infor">点击上传</el-button>
          <div slot="tip" style="color: red" class="el-upload__tip">
            {{ imageRemark }}
          </div>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('validateForm')">提交</el-button>
      </el-form-item>
    </el-row>
  </el-form>
</template>

<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

const FORM_DATA = {
  sign_rule: '',
  list: [
    {
      value: '',
      type: '',
      day: '',
      key: Date.now(),
    },
  ],
  sign_bg: '',
}
const API_SYS_CONFIG_GET = '/sys/config/info'
const API_SYS_CONFIG_UPPDATE = '/sys/config/update'

function getSysConfigInfo(payload = {}) {
  return Fetch(API_SYS_CONFIG_GET, payload)
}

function updateSysConfigInfo(payload = {}) {
  return Fetch(API_SYS_CONFIG_UPPDATE, payload)
}

function isObject(obj) {
  return Object.prototype.toString.call(obj) === '[object Object]'
}

function deepCopy(data) {
  return JSON.parse(JSON.stringify(data))
}

function objectToArray(obj) {
  if (!isObject(obj)) {
    return []
  }

  return Object.keys(obj).map(key => {
    return { label: obj[key], value: key }
  })
}

function mapPropsToFields(props) {
  const { reward_set, sign_rule, sign_bg } = props
  const form = deepCopy(FORM_DATA)

  if (isObject(reward_set)) {
    form.list = Object.keys(reward_set).map(key => {
      return {
        value: reward_set[key].value,
        type: reward_set[key].type,
        day: key,
        key: Date.now(),
      }
    })
  }

  form.sign_rule = sign_rule
  form.sign_bg = sign_bg

  return form
}

function getParams(form) {
  const { list, sign_rule, sign_bg } = form
  const reward_set = {}

  list.forEach(v => {
    reward_set[v.day] = {
      type: v.type,
      value: v.value,
    }
  })

  return {
    type: 'sign_reward',
    sign_rule,
    reward_set: JSON.stringify(reward_set),
    sign_bg,
  }
}

export default {
  data() {
    return {
      myHeaders: { Authorization: 'Bearer '+localStorage.getItem('pstt')},
      loading: false,
      form: deepCopy(FORM_DATA),
      reward_type_list: [],
      file: [],
      imageRemark: '尺寸:宽750px 高:914px 格式:png、jpg、jpeg、gif',
    }
  },
  computed: {
    ssid() {
      return localStorage.getItem('ssid')
    },
  },
  mounted() {
    this.loading = true

    getSysConfigInfo({ type: 'sign_reward' })
      .then(res => {
        if (!res || !res.data) {
          return
        }

        const { sign_reward_type, reward_set, sign_rule, sign_bg } = res.data
        this.reward_type_list = objectToArray(sign_reward_type)
        this.form = mapPropsToFields({ reward_set, sign_rule, sign_bg })
      })
      .finally(() => (this.loading = false))
  },
  methods: {
    fileSuccess(res) {
      this.form.sign_bg = res.data.file
    },
    fileSuccessBefore(file) {
      const uploadLimit = 2
      const filetype = file.name.replace(/.+\./, '')
      const uploadTypes = ['png', 'jpg', 'jpeg', 'gif']
      const isRightSize = (file.size || 0) / 1024 / 1024 < uploadLimit
      if (!isRightSize) {
        this.$message.error('文件大小超过 ' + uploadLimit + 'MB')
        return false
      }
      if (uploadTypes.indexOf(filetype.toLowerCase()) === -1) {
        this.$message.warning({
          message: '请上传后缀名为png、jpg、jpeg、gif的附件',
        })
        return false
      }
      return true
    },
    submitForm(formName) {
      this.loading = true
      this.$refs[formName].validate(valid => {
        if (!valid) {
          this.loading = false
          return
        }

        updateSysConfigInfo(getParams(this.form))
          .then(() => {
            Message.success('操作成功')
          })
          .finally(() => (this.loading = false))
      })
    },

    addConfig() {
      this.form.list.push({
        value: '',
        type: '',
        day: '',
        key: Date.now(),
      })
    },

    removeConfig(index) {
      this.form.list.splice(index, 1)
    },
  },
}
</script>

<style lang="less" scoped>
.row {
  display: flex;
}

.delete {
  /deep/ .el-form-item__content {
    margin-left: 44px !important;
  }
}
</style>
